#nav::-webkit-scrollbar-thumb {
    width: 0em;
}
#header {
    font: Regular 16px/18px Roboto;
    letter-spacing: 0.44px;
    color: #FFFFFF;
    text-transform: capitalize;
}

button {
    height: 25%;
    border: 1px solid #979797;
    background-color: transparent;
    border-radius: 8px;
    text-align: left;
    font: Regular 16px/21px Roboto;
    font-size: 0.9em !important;
    letter-spacing: 0.22px;
    color: #EBEAEA;
    text-transform: capitalize;
    outline: none;
    opacity: 1;
}

.title {
    text-align: left;
    font: Regular 2px/2px Roboto;
    letter-spacing: 0.36px;
    color: #EBEAEA;
    font-size: 15px;
    /* color: #EBEAEA; */
    text-transform: capitalize;
    opacity: 1;
}

.input {
    text-align: left;
    font: Light 20px/20px Roboto;
    font-size: 0.8em;
    letter-spacing: 0.1px;
    color: #CFD1D9 !important;
    text-transform: capitalize;
    background-color: transparent;
    border: 0;
    outline: 0;
    border-bottom: 1px solid #75798E;
    opacity: 1;
}

.input::placeholder {
    color: #a8aab3 !important;
}

.sizeBroder {
    width: 40px;
    height: 40px;
    position: sticky;
}

.input_footer {
    height: 32px;
    background: #6A6E83 0% 0% no-repeat padding-box;
    border-radius: 4px;
    opacity: 1;
    margin-top: 5px;
}

.btn_Details_container {
    height: 30px;
}

.btn_Details:hover {
    opacity: 0.7;
}
