body {
    overflow: auto;
}

.mobile {
    margin-top: calc((92vh - 595px)/2) !important;
    width: 310px !important;
    height: 575px;
    border-top-left-radius: 25px;
    border: 18px solid black;
    border-radius: 30px;
    cursor: pointer;
    overflow-y: auto;
    overflow-x: hidden;
}

.mobile_preview {
    margin-top: calc((92vh - 595px)/2) !important;
}

.input_card,
.input_card_desktop {
    display: block !important;
    background: transparent;
    border: none;
}

.input_card::-webkit-input-placeholder {
    color: #ffffff;
}

.input_card:-ms-input-placeholder {
    color: #ffffff;
}

.input_card::-ms-input-placeholder {
    color: #ffffff;
}

.input_card::placeholder {
    color: #ffffff;
}

.input_card_desktop::-webkit-input-placeholder {
    color: black;
}

.input_card_desktop:-ms-input-placeholder {
    color: black;
}

.input_card_desktop::-ms-input-placeholder {
    color: black;
}

.input_card_desktop::placeholder {
    color: black;
}

.input_about {
    display: block !important;
    background: transparent;
    border: none;
    color: #ffffff
}

.input_about::-webkit-input-placeholder, .about_card::-webkit-input-placeholder {
    color:#0d214b;
}

.input_about:-ms-input-placeholder, .about_card:-ms-input-placeholder {
    color:#0d214b;
}

.input_about::-ms-input-placeholder, .about_card::-ms-input-placeholder {
    color:#0d214b;
}

.input_about::placeholder,
.about_card::placeholder {
    color:#0d214b;
}

.about_card {
    margin: auto !important;
    z-index: 1;
    width: 100%;
    height: 70px;
    font-size: 16px;
    background: transparent;
    color: #0d214b;
    word-wrap: break-word !important;
    overflow: auto;
}

.about_card_desktop {
    background: transparent;
    letter-spacing: 0.39px;
    color: #1A181D;
    opacity: 0.8;
    min-height: 150px !important;
    max-width: 100%;
    word-wrap: break-word;
}

.about_input_card {
    letter-spacing: 0.6px;
    color: #FFFFFF;
    background: transparent;
    border: 0;
    outline: none;
    text-transform: capitalize;
    opacity: 1;
    border-bottom: 1px solid white;
    margin: auto !important;
}

.about_input_card_desktop {
    font: normal normal normal 28px/41px Heebo;
    letter-spacing: 0.39px;
    color: #1A181D;
    background: transparent;
    border: none;
    opacity: 0.8;
}

#cover {
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
}

#body_mobile {
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
}

.socialMediaWidth {
    width: 45px;
}

.footerDesktop {
    margin-top: 30px;
}

#fullName:focus,
#fullName:hover,
#role:focus,
#role:hover,
#about:focus,
#about:hover,
#cover:hover,
#logo:hover {
    opacity: 0.5
}

#profile:hover {
    border: 1px solid blue;
    opacity: 0.5
}

.icon:hover {
    border: 1px solid blue !important;
    opacity: 0.2;
}

.MuiAccordionDetails-root {
    display: flex;
    padding: 8px 8px 16px !important;
}

