.overlay {
    height: 100%;
    width: 100%;
    position: absolute;
    z-index: 1000;
    top: 0;
    right: 0;
    background-color: rgba(255, 255, 255, 255);
    overflow-x: hidden;
}

.overlay .overlay_container {
    width: calc(100% - 20px);
    height: 100%;
}

.overlay .overlay_onclos {
    width: 20px;
    height: 100%;
    background-color: #00000029;
}

.box {
    display: inline-block;
    padding: 10px;
    box-sizing: border-box;
}

.img-preview {
    overflow: hidden;
}