.MuiIconButton-label {
    width: 100%;
    display: flex;
    align-items: inherit;
    justify-content: inherit;
    color: gray;
}

.ImageLoad {
    background: transparent url('../../img/upload.png') 0% 0% no-repeat padding-box;
    background-size: cover;
    height: 50px;
    width: 65px;
    display: inline-block;
}

.Load {
    overflow-x: auto;
    width: 145px;
    white-space: nowrap;
    margin: auto;
}
