.MuiIconButton-root {
    color: rgb(0 0 0 / 8%) !important;
    padding: 0 !important;
    transition: background-color 150ms cubic-bezier(0, 0, 0, 0) 0ms !important;
    border-radius: 50%;
    width: 40px !important;
    height: 40px !important;
}

